/* Custom SCSS for Minz Website - Literary Theme */
/* Extracted from globals.css and tailwind.config.js */

/* ===== FONT IMPORTS ===== */
@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:ital,wght@0,400;0,500;0,600;0,700;0,800;0,900;1,400;1,500;1,600;1,700;1,800;1,900&family=Source+Sans+Pro:ital,wght@0,200;0,300;0,400;0,600;0,700;0,900;1,200;1,300;1,400;1,600;1,700;1,900&display=swap');

/* ===== CSS CUSTOM PROPERTIES ===== */
:root {
  /* Literary Color Palette - Primary (Navy) */
  --primary-50: #f8fafc;
  --primary-100: #f1f5f9;
  --primary-200: #e2e8f0;
  --primary-300: #cbd5e1;
  --primary-400: #94a3b8;
  --primary-500: #475569;
  --primary-600: #334155;
  --primary-700: #1e293b;
  --primary-800: #0f172a;
  --primary-900: #020617;
  --primary-950: #010409;

  /* Secondary (Warm Tones) */
  --secondary-50: #fdf8f6;
  --secondary-100: #f7f3e9;
  --secondary-200: #f0e6d2;
  --secondary-300: #e7d3a3;
  --secondary-400: #dbc171;
  --secondary-500: #d4c5a9;
  --secondary-600: #c5b091;
  --secondary-700: #a8956f;
  --secondary-800: #8b7c5a;
  --secondary-900: #73664a;
  --secondary-950: #5c533c;

  /* Accent (Brass/Gold) */
  --accent-50: #fefce8;
  --accent-100: #fef3c7;
  --accent-200: #fed7aa;
  --accent-300: #fdba74;
  --accent-400: #fb923c;
  --accent-500: #f59e0b;
  --accent-600: #d97706;
  --accent-700: #b45309;
  --accent-800: #92400e;
  --accent-900: #78350f;
  --accent-950: #451a03;

  /* Charcoal */
  --charcoal-50: #f7f7f7;
  --charcoal-100: #e3e3e3;
  --charcoal-200: #c8c8c8;
  --charcoal-300: #a4a4a4;
  --charcoal-400: #818181;
  --charcoal-500: #666666;
  --charcoal-600: #515151;
  --charcoal-700: #434343;
  --charcoal-800: #383838;
  --charcoal-900: #2d2d2d;
  --charcoal-950: #1a1a1a;

  /* Parchment */
  --parchment-50: #fefdfb;
  --parchment-100: #fdf9f0;
  --parchment-200: #faf4e6;
  --parchment-300: #f7f0dc;
  --parchment-400: #f2e6c7;
  --parchment-500: #ecddb3;
  --parchment-600: #e3d09f;
  --parchment-700: #d7c28b;
  --parchment-800: #cab477;
  --parchment-900: #bda663;
  --parchment-950: #b0984f;
}

/* ===== GLOBAL BASE STYLES ===== */
html,
body {
  padding: 0;
  margin: 0;
  font-family: 'Source Sans Pro', -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, sans-serif;
  scroll-behavior: smooth;
  color: var(--charcoal-900);
}

a {
  color: inherit;
  text-decoration: none;
}

* {
  box-sizing: border-box;
}

/* ===== CUSTOM SCROLLBAR ===== */
::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: var(--primary-100);
}

::-webkit-scrollbar-thumb {
  background: var(--primary-300);
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: var(--primary-400);
}

/* ===== LITERARY BUTTON COMPONENTS ===== */
.btn-primary {
  display: inline-flex;
  align-items: center;
  padding: 0.75rem 1.5rem;
  border: 1px solid transparent;
  font-size: 1rem;
  font-weight: 500;
  border-radius: 0.5rem;
  color: var(--parchment-50);
  background-color: var(--primary-700);
  transition: all 0.3s ease;
  cursor: pointer;
  text-decoration: none;
  box-shadow: 0 2px 8px rgba(15, 23, 42, 0.15), 0 1px 3px rgba(15, 23, 42, 0.1);
}

.btn-primary:hover {
  background-color: var(--primary-800);
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(15, 23, 42, 0.2), 0 2px 6px rgba(15, 23, 42, 0.15);
}

.btn-primary:focus {
  outline: none;
  box-shadow: 0 0 0 3px var(--accent-500), 0 4px 12px rgba(15, 23, 42, 0.2), 0 2px 6px rgba(15, 23, 42, 0.15);
}

// Footer company link underline
footer a[href*="apexridge.ventures"] {
  text-decoration: underline !important;
}

.btn-secondary {
  display: inline-flex;
  align-items: center;
  padding: 0.75rem 1.5rem;
  border: 2px solid var(--primary-700);
  font-size: 1rem;
  font-weight: 500;
  border-radius: 0.5rem;
  color: var(--primary-700);
  background-color: var(--parchment-50);
  transition: all 0.3s ease;
  cursor: pointer;
  text-decoration: none;
  box-shadow: 0 2px 4px rgba(15, 23, 42, 0.08);
}

.btn-secondary:hover {
  background-color: var(--parchment-100);
  transform: translateY(-1px);
  box-shadow: 0 4px 8px rgba(15, 23, 42, 0.12), 0 2px 4px rgba(15, 23, 42, 0.08);
}

.btn-accent {
  display: inline-flex;
  align-items: center;
  padding: 0.75rem 1.5rem;
  border: 1px solid transparent;
  font-size: 1rem;
  font-weight: 500;
  border-radius: 0.5rem;
  color: var(--primary-800);
  background-color: var(--accent-500);
  transition: all 0.3s ease;
  cursor: pointer;
  text-decoration: none;
  box-shadow: 0 2px 8px rgba(180, 83, 9, 0.15), 0 1px 3px rgba(180, 83, 9, 0.1);
}

.btn-accent:hover {
  background-color: var(--accent-600);
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(180, 83, 9, 0.2), 0 2px 6px rgba(180, 83, 9, 0.15);
}

/* ===== LAYOUT COMPONENTS ===== */
.container {
  max-width: 80rem;
  margin: 0 auto;
  padding: 0 1rem;
}

@media (min-width: 640px) {
  .container {
    padding: 0 1.5rem;
  }
}

@media (min-width: 1024px) {
  .container {
    padding: 0 2rem;
  }
}

.section-padding {
  padding: 5rem 0;
}

@media (min-width: 640px) {
  .section-padding {
    padding: 7rem 0;
  }
}

/* ===== LITERARY BACKGROUNDS AND TEXTURES ===== */
.paper-texture {
  background-image:
    radial-gradient(circle at 20% 80%, rgba(120, 119, 198, 0.02) 0%, transparent 50%),
    radial-gradient(circle at 80% 20%, rgba(255, 255, 255, 0.03) 0%, transparent 50%),
    radial-gradient(circle at 40% 40%, rgba(212, 197, 169, 0.02) 0%, transparent 50%);
}

.parchment-bg {
  background: linear-gradient(135deg, var(--parchment-50) 0%, var(--secondary-50) 50%, var(--parchment-100) 100%);
}

.navy-gradient {
  background: linear-gradient(135deg, var(--primary-700) 0%, var(--primary-800) 50%, var(--primary-900) 100%);
}

.literary-card {
  background-color: white;
  border: 1px solid var(--secondary-200);
  border-radius: 0.75rem;
  box-shadow: 0 4px 6px -1px rgba(15, 23, 42, 0.08), 0 2px 4px -1px rgba(15, 23, 42, 0.03);
  transition: box-shadow 0.3s ease;
}

.literary-card:hover {
  box-shadow: 0 10px 15px -3px rgba(15, 23, 42, 0.08), 0 4px 6px -2px rgba(15, 23, 42, 0.03);
}

/* ===== TYPOGRAPHY UTILITIES ===== */
.heading-primary {
  font-family: 'Playfair Display', serif;
  font-weight: 700;
  color: var(--primary-900);
  line-height: 1.2;
}

.heading-secondary {
  font-family: 'Playfair Display', serif;
  font-weight: 600;
  color: var(--primary-800);
}

.text-literary {
  font-family: 'Source Sans Pro', sans-serif;
  color: var(--charcoal-800);
  line-height: 1.7;
}

.font-heading {
  font-family: 'Playfair Display', serif;
}

.font-sans {
  font-family: 'Source Sans Pro', sans-serif;
}

/* ===== ANIMATION UTILITIES ===== */
@keyframes fadeIn {
  0% { opacity: 0; }
  100% { opacity: 1; }
}

@keyframes slideUp {
  0% { transform: translateY(20px); opacity: 0; }
  100% { transform: translateY(0); opacity: 1; }
}

@keyframes slideDown {
  0% { transform: translateY(-20px); opacity: 0; }
  100% { transform: translateY(0); opacity: 1; }
}

.animate-fade-in {
  animation: fadeIn 0.5s ease-in-out forwards;
}

.animate-slide-up {
  animation: slideUp 0.5s ease-out forwards;
}

.animate-slide-down {
  animation: slideDown 0.5s ease-out forwards;
}