/* Import custom styles with color variables and components */
@import 'custom';

/* Import Tailwind-style utility classes */
@import 'tailwind-utilities';

/* Additional literary theme enhancements */
body {
  font-family: 'Source Sans Pro', -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, sans-serif;
  background: linear-gradient(135deg, var(--parchment-50) 0%, var(--secondary-100) 100%);
  min-height: 100vh;
}

/* Ensure proper font loading */
.font-heading,
h1, h2, h3, h4, h5, h6 {
  font-family: 'Playfair Display', serif;
}

.font-sans,
p, span, div, a, button {
  font-family: 'Source Sans Pro', sans-serif;
}